import { Asset } from "../../types/accounts"

type AssetsState = Asset[];

interface AssetsAction {
  type: string,
  asset: Asset,
}

export default (state: AssetsState = [], action: AssetsAction): AssetsState => {
  switch (action.type) {
    case 'ASSETS_NEW':
      return [...state, action.asset];
    default:
      return state;
  }
};