import { CurrencyEnum } from '../../types/enums';
import { ReduxThunk } from '../../types/thunk';
import { Asset } from '../../types/accounts';

export const addAsset = (
  name: string,
  value: number,
  currency: CurrencyEnum
): ReduxThunk => (dispatch, getState) => {
  const exchangeRate = getState().exchangeRates[currency];
  const valueUSD = value * exchangeRate;

  const newAsset: Asset = { name, value, currency, valueUSD };

  dispatch({ type: 'ASSETS_NEW', asset: newAsset });
};
