import { Action } from "redux"

export type ExchangeRates = {
  USD: number,
  UAH: number,
  EUR: number,
}

const defaultState: ExchangeRates = {
  USD: 1,
  UAH: 0.07,
  EUR: 1.07,
}

export default (state: ExchangeRates = defaultState, action: Action): ExchangeRates => {
  switch (action.type) {
    default:
      return state;
  }
}